import Vue from 'vue'
import Router from 'vue-router'
import data from '@/components/data.vue'
import About from '@/components/About.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'data',
      component: data
    },
    {
      path: '/about',
      name: 'data',
      component: About
    }
  ]
})



