<template>
  <div id="result">
    <div style="padding-top: 0px;background-color:#f4f4f4;min-height:100vh;">
      <div class="loan_amount">
        <p>贷款金额</p>
        <h3 v-text="toThousands(($route.query.loanAmountCal*1).toFixed(2))"></h3>
      </div>
      <div class="detail">
        <p>总利息：<font color="#fd7272">￥{{toThousands(($route.query.totalCustomerInterest*1).toFixed(2))}}</font></p>
        <p>贷款期限：<font color="#333334">{{$route.query.terms}}期</font></p>
        <p>首付比例：<font color="#333334">{{$route.query.downPaymentRate*100}}%</font></p>
        <p>尾款比例：<font color="#333334">{{$route.query.remainingMoneyRate*100}}%</font></p>
      </div>
      <div class="list" @click="clickSelect(1)">
        <p>首付金额</p>
        <span class="item-note">￥{{toThousands(($route.query.downPaymentAmountCal*1).toFixed(2))}}</span>
      </div>
      <div style="width:92vw;height:1px;background-color:#f5f5f5;margin:0 auto;"></div>
      <div class="list" @click="clickSelect(1)">
        <p>预估月供</p>
        <span class="item-note">￥{{toThousands(($route.query.monthlySupply*1).toFixed(2))}}</span>
      </div>
      <div style="width:92vw;height:1px;background-color:#f5f5f5;margin:0 auto;"></div>
      <div class="list" @click="clickSelect(1)">
        <p>尾款金额</p>
        <span class="item-note">￥{{toThousands(($route.query.tail*1).toFixed(2))}}</span>
      </div>
      <router-link to="/" style="width: 100vw;text-decoration:none;">
        <div class="button">重新计算</div>
        <!-- <button class="button button-assertive button-block"></button> -->
      </router-link>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      
    }
  },
  methods: {
    toThousands(num) {
      let a = num.toString().split ('.')
      return a[0].replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')+'.'+a[1]
    }
  }
};
</script>
<style>
.grade-a .item {
  color: #6d6d6d;
}
.item .item-note {
  color: #18191a;
}
#result .loan_amount{
  background-color: #fff;
  text-align: center;
  padding-top: 8vw;
  padding-bottom: 6vw;
}
#result .loan_amount p{
  color: #3a4350;
  font-size: 4.5vw;
  margin: 0;
}
#result .loan_amount h3{
  margin: 0;
  margin-top: 2vw;
  color: #fc5555;
  font-size: 7vw;
}
#result .detail{
  background-color: #fff;
  overflow: hidden;
  margin: 3vw 0;
  padding: 4vw 0;
}
#result .detail p{
  float: left;
  width: 42vw;
  margin: 0;
  padding: 1vw 4vw;
  color: #6d6d6d;
  font-size: 4vw;
}

#result .list{
  position: relative;
  height: 14vw;
  background-color: #fff;
}
#result .list p{
  position: absolute;
  margin: 0;
  line-height: 14vw;
  font-size: 4.5vw;
  left: 4vw;
  color: #6d6d6d;
}
#result .list span{
  position: absolute;
  line-height: 14vw;
  font-size: 4.5vw;
  right: 4vw;
  color: #171819;
}
#result .list img{
  position: absolute;
  width: 2.5vw;
  top: 4.4vw;
  right: 4vw;
}
.button{
  background-color: #ba6954;
  width: 86vw;
  line-height: 12vw;
  font-size: 4.5vw;
  margin: 10vw auto 0;
  text-align: center;
  color: #fff;
}
</style>


